@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
html,
body {
  font-family: 'Varela Round', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: none;
}

body {
  position: relative;
}

#root {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
}

input,
button,
textarea {
  font-family: 'Varela Round', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  color: black;
}
.list_users {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
    justify-content: center;
}
.user-avatar__image {
    border-radius: 50%;
    width: 100px;
    height: 100px;
    margin: 10px 0;
}
.user-item {
    box-sizing: border-box;
    text-align: center;
    background-color: #3d405b;
    border: 2px solid #3d405b;
    margin: 10px 10px;
    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    width: 175px;
}

.user-item:hover {
    -webkit-animation: hovercard .3s ease-in-out forwards;
            animation: hovercard .3s ease-in-out forwards;
}

@-webkit-keyframes hovercard {
    from {
        background-color: #3d405b;
    }

    to {
        background-color: white;
        border: 2px solid #3d405b;
        box-shadow: 3px 7px 14px 2px rgba(0, 0, 0, 0.5);
    }
}

@keyframes hovercard {
    from {
        background-color: #3d405b;
    }

    to {
        background-color: white;
        border: 2px solid #3d405b;
        box-shadow: 3px 7px 14px 2px rgba(0, 0, 0, 0.5);
    }
}

.user-item__name,
.user-item__count {
    color: white;
}

h2 {
    font-size: 16px;
}

h3 {
    font-size: 12px;
}


.user-item:hover h2,
.user-item:hover h3 {
    color: #3d405b;
}
.error-text {
    color: black;
}
.form-wrapper {
    box-sizing: border-box;
    background-color: #3d405b;
    border: 2px solid #3d405b;
    margin: 10px 10px;
    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    display: block;
    margin: 3rem auto;
    width: 600px;
    padding: 20px;
    color: white;
}

.form-control {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

label {
    margin-bottom: 4px;
}

input {
    height: 20px;
}

input,
textarea {
    border: none;
    border-radius: 12px;
    background-color: rgba(255, 255, 255, 0.16);
    padding: 10px;
    color: white;
}

.error-text {
    color: indianred;
}
#outer-container {
    height: 70px;
}

/* Position and sizing of burger button */
.bm-burger-button {
    position: fixed;
    width: 36px;
    height: 30px;
    right: 20px;
    top: 20px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
    background: white;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
    background: #f6bed6;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
    height: 24px;
    width: 24px;
    left: 8px;
}

/* Color/shape of close button cross */
.bm-cross {
    background: #bdc3c7;
}

/*
  Sidebar wrapper styles
  Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
  */
.bm-menu-wrap {
    position: fixed;
    height: 100%;
}

/* General sidebar styles */
.bm-menu {
    background: #373a47;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
    fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
    color: #b8b7ad;
    height: auto !important;
}

/* Individual item */
.bm-item {
    display: inline-block;
    text-decoration: none;
    color: white;
    margin-bottom: 10px;
    outline: none;
    padding-bottom: 2px;
}

/* Styling of overlay */
.bm-overlay {
    background: rgba(0, 0, 0, 0.3);
}

.menu-header-item {
    outline: none;
    color: white;
    text-align: center;
}

.activeLink {
    border-bottom: 2px solid white !important;
}

#outer-container {
    background-color: #204051;
    position: fixed;
    top: 0;
    width: 100%;
}
.footer {
    right: 0;
    left: 0;
    bottom: 0;
    padding: 1rem;
    background-color: #204051;
    text-align: center;
    margin: 0;
    display: flex;
    justify-content: space-around;
    margin-top: 3rem;
}

.footer__left {
    text-align: left;
}

.footer__right {
    text-align: left;   
    display: flex;
    align-items: center;
}

p {
    color: white;
}
.main-content {
    margin-top: 5rem;
}
li {
    margin: 0 auto;
    text-align: center;
    width: 380px;
    border-radius: 12px;
    overflow: hidden;
    box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.3);
    background-color: #336d88;
    padding: 0 0 20px 0;
}

.place-item-wrapper {
    width: 100%;
    display: inline-block;
}

.place-item-title {
    color: white;
    margin: 15px;
}

img {
    height: 260px;
}

.icon-button {
    position: static;
}
.dialog__map {
    height: 100vh;
    width: 100%;
}
.dialog_title-title {
    color: black;
    margin: 0;
}
ul {
    list-style: none;
    display: flex;
    padding: 0;
}
